export const validation = (value, type) => {
  const letters = /[A-z]/
  const numbers = /[0-9]/
  if(value) {
    switch (type) {
      case 'phone':
        return value.length > 11 && value.match(numbers) && value.includes('+') ? '' : '  phone'
      case 'email':
        return value.includes('@') && value.includes('.') ? '' : '  email'
      case 'password':
        return value.length > 6 && value.match(numbers) && value.match(letters) ? '' : '    6       '
      case 'firstName':
        return value.length > 1 && value.match(letters) ? '' : '  '
      case 'lastName':
        return value.length > 1 && value.match(letters) ? '' : '  '
      default:
        return ''
    }
  } return ' '
}